import {Component, Input, OnChanges} from '@angular/core';
import {Observable} from 'rxjs/Rx';
// SERVICE
import {EmitterServiceService} from '../emitter-service.service';
// MODEL
import {Trainer} from '../model/Trainer';

@Component({
  selector: 'app-trainer-form',
  templateUrl: './trainer-form.component.html',
  // styleUrls: ['./trainer-form.component.css']
})

export class TrainerFormComponent implements OnChanges{
  private model = new Trainer(new Date(), '', '', '', null);
  private editing = false;
  @Input() trainer: Trainer;
  @Input() trainereditId: string;
  @Input() trainerlistId: string;

  constructor(
      private EmitterServiceService: EmitterServiceService
  ) {}

  submitTrainer() {
    // Variable to hold a reference of addComment/updateComment
    let trainerOperation: Observable <Trainer[]>;

    if (!this.editing) {
      // Create a new pokemon
      trainerOperation = this.EmitterServiceService.addTrainer(this.model);
    } else {
      // Update an existing pokemon
      trainerOperation = this.EmitterServiceService.updateTrainer(this.model);
    }
    // console.log(trainerOperation);

    // Subscribe to observable
    trainerOperation.subscribe(
        trainer => {
          // Emit list event
          EmitterServiceService.get(this.trainerlistId).emit(trainer);
          // Empty model
          this.model = new Trainer(new Date(), '', '', '', null);
          // Switch editing status
          if (this.editing) {
            this.editing = !this.editing;
          }
        },
        err => {
          // Log errors if any
          console.log(err);
        });
  }
  editTrainer() {
    // Emit edit event
    EmitterServiceService.get(this.trainereditId).emit(this.trainerlistId);
    this.model = this.trainer;
    this.editing = true;
  }

  // Deze gaat af bij de edit knop
  ngOnChanges() {
    EmitterServiceService.get(this.trainereditId).subscribe((trainer: Trainer) => {
      this.model = trainer;
      this.editing = true;
    });
  }
}



