import {Component, Input, OnChanges, SimpleChanges} from '@angular/core';
import {EmitterServiceService} from '../emitter-service.service';
// MODEL
import {Trainer} from '../model/Trainer';

@Component({
    selector: 'app-trainer-list',
    templateUrl: './trainer-list.component.html',
    // styleUrls: ['./trainer-list.component.css']
})
export class TrainerListComponent implements OnChanges {
    private model = new Trainer(new Date(), '', '', '', null);
    private editing = false;
    @Input() trainer: Trainer[];
    @Input() trainerlistId: string;
    @Input() trainereditId: string;

    constructor(
    private EmitterServiceService: EmitterServiceService
  ) {}
    loadTrainers() {
        // Get all pokemon from the service
        this.EmitterServiceService.getTrainers()
            .subscribe(
                trainer => this.trainer = trainer, // Bind to view/html component
                err => {
                    console.log(err); // Log errors als er zijn
                });
    }
    editTrainer() {
        // Emit edit event
        EmitterServiceService.get(this.trainereditId).emit(this.trainer);
    }

    ngOnInit() {
        // Vuur de load trainer functie uit
        this.loadTrainers();
    }
    ngOnChanges(changes: SimpleChanges) {
        this.loadTrainers();
        EmitterServiceService.get(this.trainerlistId).subscribe(
        trainers => this.trainer = trainers, // Bind to view/html component
        err => {
          console.log(err); // Log errors als er zijn
        });
        EmitterServiceService.get(this.trainereditId).subscribe((trainer: Trainer) => {
            this.model = trainer;
            this.editing = true;
        });
    }
}



