import {EventEmitter, Injectable} from '@angular/core';
import {Headers, Http, RequestOptions, Response} from '@angular/http';
import {Observable} from 'rxjs/Rx';
// MODEL
import {Pokemon} from './model/Pokemon';
import {Trainer} from './model/Trainer';
// MAPPING & ERROR HANDLING
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class EmitterServiceService {
    // data emitter
    private static _emitters: { [ID: string]: EventEmitter<any> } = {};
    // POKEMON DATA URL
    private url = 'https://599748ee3c298d0011c5de65.mockapi.io/api/pokemons';
    // TRAINER DATA URL
    private trainerurl = 'https://599748ee3c298d0011c5de65.mockapi.io/api/trainers';

    // event emitter
    static get(ID: string): EventEmitter<any> {
        if (!this._emitters[ID]) {
            this._emitters[ID] = new EventEmitter();
        }
        return this._emitters[ID];
    }
    constructor (private http: Http) {}

     // Fetch all existing pokemon
     getPokemon(): Observable<Pokemon[]> {
         // ...using get request
         return this.http.get(this.url)
             // ...and calling .json() on the response to return data
             .map((res: Response) => res.json())
             // ...errors if any
             .catch((error: any) => Observable.throw(error.json().error || 'Error - Port not running'));
             }
     // Add a new pokemon
     addPokemon (body: Object): Observable<Pokemon[]> {
        // let bodyString = JSON.stringify(body); // Stringify payload
        const headers = new Headers({ 'Content-Type': 'application/json'}); // ... Set content type to JSON
        const options = new RequestOptions({ headers: headers }); // Create a request option
        return this.http.post(this.url, body, options) // ...using post request
            // ...and calling .json() on the response to return data
            .map((res: Response) => res.json())
            // ...errors if any
            .catch((error: any) => Observable.throw(error.json().error || 'Error - Adding failed, data is inserted tho..'));
     }
     // Update pokemon
     updatePokemon (body: Object): Observable<Pokemon[]> {
        // let bodyString = JSON.stringify(body); // Stringify payload
        const headers = new Headers({ 'Content-Type': 'application/json' }); // ... Set content type to JSON
        const options = new RequestOptions({ headers: headers }); // Create a request option
        return this.http.put(`${this.url}/${body['id']}`, body, options) // ...using put request
                         .map((res: Response) => res.json()) // ...and calling .json() on the response to return data
                         .catch((error: any) => Observable.throw(error.json().error || 'Error - Updating failed')); // ...errors if any
     }
     // Delete pokemon
     removePokemon (id: string): Observable<Pokemon[]> {
        return this.http.delete(`${this.url}/${id}`) // ...using put request
                         .map((res: Response) => res.json()) // ...and calling .json() on the response to return data
                         .catch((error: any) => Observable.throw(error.json().error || 'Error - Removing failed')); // ...errors if any
     }

     // Fetch all existing trainers
     getTrainers(): Observable<Trainer[]>{
        // ...using get request
        return this.http.get(this.trainerurl)
        // ...and calling .json() on the response to return data
            .map((res: Response) => res.json())
            // ...errors if any
            .catch((error: any) => Observable.throw(error.json().error || 'Error - Port not running'));
     }
     // Add a new Trainer
     addTrainer (body: Object): Observable<Trainer[]> {
        // let bodyString = JSON.stringify(body); // Stringify payload
        const headers = new Headers({ 'Content-Type': 'application/json'}); // ... Set content type to JSON
        const options = new RequestOptions({ headers: headers }); // Create a request option
        return this.http.post(this.trainerurl, body, options) // ...using post request
            // ...and calling .json() on the response to return data
            .map((res: Response) => res.json())
            // ...errors if any
            .catch((error: any) => Observable.throw(error.json().error || 'Error - Adding failed, data is inserted tho..'));
     }
     // Update trainer
     updateTrainer (body: Object): Observable<Trainer[]> {
        // let bodyString = JSON.stringify(body); // Stringify payload
        const headers = new Headers({ 'Content-Type': 'application/json' }); // ... Set content type to JSON
        const options = new RequestOptions({ headers: headers }); // Create a request option
        return this.http.put(`${this.trainerurl}/${body['id']}`, body, options) // ...using put request
            .map((res: Response) => res.json()) // ...and calling .json() on the response to return data
            .catch((error:any) => Observable.throw(error.json().error || 'Error - Updating failed')); // ...errors if any
     }
     // Delete trainer
     removeTrainer (id: string): Observable<Trainer[]> {
        return this.http.delete(`${this.trainerurl}/${id}`) // ...using put request
            .map((res: Response) => res.json()) // ...and calling .json() on the response to return data
            .catch((error: any) => Observable.throw(error.json().error || 'Error - Removing failed')); // ...errors if any
     }
}



